*{
    box-sizing: border-box;
}

main{
    @include fullscreen;
    padding-top: 26vw;
}

header{
    width: 100%;
    nav{
        width: 100%;
        display: flex;
        justify-content: center;
    }
}

.logo{
    position: fixed;
    margin: 3vh auto;
    z-index: 2;
    img{
        width: 10vw;
        max-width: 80px;
    }
}

a.left-nav, a.right-nav{
    font-family: 'Knewave', 'arial black', cursive;
    font-size: 5vw;
    text-transform: uppercase;
    color: #162F98;
    text-decoration: none;
    position: fixed;
    z-index: 1000;
    top: 50%;
    transition: .3s ease;
}
  
.left-nav{
    left: -4.2vw;
    transform: rotate(-90deg);
    &:hover{
        transform: rotate(-80deg);
    }
}
.right-nav{
    right: -1vw;
    transform: rotate(90deg);
    &:hover{
        transform: rotate(80deg);
    }
}

h1{
    font-family: 'Knewave', arial, cursive;
    font-size: 5vw;
    margin-top: 0;
}
h2, h3{
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    margin-top: 0;
    margin-bottom: 3px;
}
h2{
    margin-bottom: 18px;
}

p{
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    font-size: 1rem;
    line-height: 19px;
    margin-top: 0;
}
ul{
    margin-top: 0;
}
li p{
    margin-bottom: 0;
}

