.portfolio-page main {
    padding-top: 0;
    img {
        width: 100%; 
        height: auto; 
        display: block;
    }
    h2{
        font-family: 'Knewave', cursive;
        font-size: 1.6rem;
        margin-top: 0;
    }
}

.project {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    width: 100%;
    margin-bottom: 8vw;
    .project-overlay {
        bottom: 6vw;
        width: 80%;
        padding: 4vw;
        position: relative;
    }
    .project-text {
        text-align: center;
        color: white;
        margin-top: 0;
        p{
            margin-bottom: 0;
        }
    }

    &.project-grid {
        .project-overlay {
            background-color:#CD474E;
        }
    }
    &.project-lolla {
        .project-overlay {
            background-color: #B25367;
        }
    }
    &.project-finn {
        .project-overlay {
            background-color: #232323;
        }
    }
    &.project-salto {
        .project-overlay {
            background-color: #C65306;
        }
    }
    &.project-tallbarret {
        .project-overlay {
            background-color: #007167;
        }
    }
    &.project-brus {
        .project-overlay {
            background-color: #232323;
        }
    }
    
}


