.start-page{
    // Fallback
    background: rgb(251,232,199);
    background: url('../img/noise.jpg'), -moz-linear-gradient(228deg, rgba(251,232,199,1) 0%, rgba(231,173,207,1) 22%, rgba(203,233,253,1) 63%, rgba(253,195,222,1) 80%, rgba(253,219,194,1) 100%);
    background: url('../img/noise.jpg'), -webkit-linear-gradient(228deg, rgba(251,232,199,1) 0%, rgba(231,173,207,1) 22%, rgba(203,233,253,1) 63%, rgba(253,195,222,1) 80%, rgba(253,219,194,1) 100%);
    
    background: linear-gradient(228deg, rgba(251,232,199,1) 0%, rgba(231,173,207,1) 22%, rgba(203,233,253,1) 63%, rgba(253,195,222,1) 80%, rgba(253,219,194,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#fbe8c7",endColorstr="#fddbc2",GradientType=1);

    // Gradient with noise
    background: url('../img/noise.jpg'), linear-gradient(228deg, rgba(251,232,199,1) 0%, rgba(231,173,207,1) 22%, rgba(203,233,253,1) 63%, rgba(253,195,222,1) 80%, rgba(253,219,194,1) 100%);
    background-blend-mode: multiply;
    background-size: cover;
    .logo{
        display: none;
    }
    main{
        padding-top: 0;
    }
}
.issa{
    position: absolute;
    @include fullscreen;
    div{
        width: 200px;
        height: 300px;
        position: absolute;
        background: url('../img/issa-letters.svg');
        background-size: 573px 301px;
        transform: scale(.7);
        transition: .3s ease;
        &:hover{
            transform: scale(.75);
        }
    }
    .letter-i{
        background-position: 0 0;
        top: 5px;
        left: 10vw;   
    }
    .letter-s{
        background-position: -180px 0;
        top: 14vh;
        right: 20vw;
    }
    .letter-ss{
        background-position: -180px 0;
        bottom: 25vh;
        left: 20vw
    }
    .letter-a{
        background-position: -370px 0;
        bottom: 5vh;
        right: 10vw;
    }
}